import React, { lazy, Suspense } from 'react'
import {BrowserRouter, Routes, Route } from 'react-router-dom'
import "./App.css"
import { PulseLoader } from 'react-spinners'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'typeface-crimson-text'
import 'typeface-playfair-display'
import 'typeface-montserrat'

const Home = lazy(() => import("./pagine/Home.js"))
const Canto = lazy(() => import("./pagine/Canto.js"))
const Analisi = lazy(() => import("./pagine/Analisi.js"))
const InformazioniLegali = lazy(() => import("./pagine/InformazioniLegali.js"))
const Crediti = lazy(() => import("./pagine/Crediti.js"))
const Errore = lazy(() => import("./pagine/Errore.js"))

const App = () => {
	return (
		<BrowserRouter>
			<Suspense fallback={<div className='container d-flex justify-content-center align-items-center centra2'><PulseLoader color="#ffc06c" size={20} /></div>}>
				<Routes>
					<Route path="/" element={<Home />} />
					<Route path="/canto" element={<Canto />} />
					<Route path="/analisi" element={<Analisi />} />
					<Route path="/crediti" element={<Crediti />} />
					<Route path="/informazioni-legali" element={<InformazioniLegali />} />
					<Route path="*" element={<Errore />} />
				</Routes>
			</Suspense>
		</BrowserRouter>
	)
}

export default App